import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { useObserver } from "mobx-react-lite";
import stateContext from "../../state";
import Button from "./button";
import PanelBtn from "../../components/panel-button";
import withApiWrapper from "../../utils/withApiWrapper";
import { getUserId, clearUserId, clearUserToken } from "../../utils/session";
import { privatePath, publicPath } from "../../utils/route-constants";

import logo from "../../images/logo.png";
import avatar from "../../images/avatar.png";
import dashboard from "../../images/dashboard.png";
import logout from "../../images/logout.png";
import withdraw from "../../images/withdraw.png";
import log from "../../images/log.png";
import setting from "../../images/setting.png";

const StyledImg = styled.img`
  width: 45px;
  border-style: none;
  cursor: pointer;
  @media (min-width: 992px) {
    position: absolute;
    left: 50%;
  }
`;

const Avatar = styled.img`
  margin: 0px 12px;
  width: 25px;
`;

const ButtonDiv = styled.div`
  display: flex;
  width: fit-content;
  @media (max-width: 991px) {
    ${props => props.mHidden && "display: none;"}
  }
`;

const DeskTopWrapper = styled.div`
  width: 100%;
  height: 42px;
  display: flex;
  justify-content: space-between;
`;

const MobileRowWrapper = styled.div`
  display: flex;
  justify-content: center;
  margin: 0 auto;
  margin-top: 15px;
  padding-top: 15px;
  width: calc(100%);
  align-self: center;
  border-top: 1px solid #ddd;
  @media (min-width: 992px) {
    display: none;
  }
`;

const UserDetailWrapper = styled.div`
right: 0px;
color: #333;
align-self: center;
font-family: 'Roboto'
  width: fit-content;
  display: flex;
  justify-content: space-between;
  @media (min-width: 992px) {
    margin-left: auto;
  }
`;

const StyledLink = styled(Link)`
  :hover {
    text-decoration: none;
  }
`;

const Row = styled.div`
  margin: auto;
  @media (min-width: 767px) {
    [class*="col-"]:nth-child(3n-2) {
      padding: 5px;
    }
    [class*="col-"]:nth-child(3n-1) {
      padding: 5px;
    }
    [class*="col-"]:nth-child(3n) {
      padding: 5px;
    }
  }

  @media (max-width: 768px) {
    .detail-col .row [class*="col-"]:nth-child(2n-1) {
      padding: 5px;
    }
    .detail-col .row [class*="col-"]:nth-child(2n) {
      padding: 5px;
    }
  }
`;

const MenuHamburger = styled.div`
  margin-left: 10px;
  align-items: center;
  display: flex;
  background-color: #333;
  justify-content: right;
  float: right;
  width: auto;
  padding: 8px 12px;
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
`;

const OutSideBar = styled.div`
  // background-color: white;
  // position: absolute;
  // top: -15px;
  // right: 0;
  // display: -webkit-box;
  // display: flex;
  // flex-direction: column;
  // width: 320px;
  // max-width: 440px;
  // height: 100vh;
  // box-shadow: 2px 0 4px rgba(0, 0, 0, 0.3);
  // overflow: hidden;
  // z-index: 9999;
`;

const SideBar = styled.div`
  height: 100%;
  width: 320px;
  position: fixed;
  z-index: 999;
  top: 0;
  right: -320px;
  background-color: rgba(255, 255, 255, 0.95);
  overflow-x: hidden;
  box-shadow: 1px 0 5px #c0c0c0;
  padding-top: 20px;
  // opacity: 0;
  transition: 0.5s ease-out;

  ${props =>
    props.active &&
    `opacity: 1;
 right:0;`}
`;

const Overlay = styled.div`
  ${props => !props.active && "display:none;"}
  position: fixed;
  top: 0;
  left: -320px;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  opacity: 0;
  // transition: 1s ease-out;
  ${props =>
    props.active &&
    `opacity: 0.8;  
  left: 0;
  visibility: visible;
  z-index: 90;`}
`;

const Iwrapper = styled.div`
  display: flex;
  justify-content: flex-end;
  margin-bottom: 5px;
  i {
    margin-right: 17px;
    cursor: pointer;
  }
`;

const ButtonGroup = ({ mHidden }) => {
  return (
    <ButtonDiv mHidden={mHidden}>
      <StyledLink to={privatePath.dashboard}>
        <Button text="หน้าแรก" iconClass="fas fa-home" />
      </StyledLink>

      <Button
        text="ไปหน้าเล่นเกม"
        iconClass="fas fa-gamepad"
        marginLeft={10}
        invertColor
      />
      <Button text="โปรโมชั่น" iconClass="fas fa-percentage" marginLeft={10} />
    </ButtonDiv>
  );
};

const PrivateNavbar = ({ apiCall, location: { pathname }, history }) => {
  const { userInfo, setUserInfo } = React.useContext(stateContext);
  const [showSidebar, setShowSidebar] = React.useState(false);
  const closeSideBar = () => setShowSidebar(false);
  const fetchUserInfo = async () => {
    const res = await apiCall({
      method: "get",
      path: `/wallet-owner/${getUserId()}`
    });
    if (res) {
      setUserInfo(res);
    }
  };
  React.useEffect(() => {
    if (pathname !== privatePath.dashboard) fetchUserInfo();
  }, []);
  return useObserver(() => {
    const { email, amount } = userInfo;
    return (
      <>
        <DeskTopWrapper>
          <ButtonGroup mHidden={true} />
          <Link to={privatePath.dashboard}>
            <StyledImg src={logo} />
          </Link>
          <UserDetailWrapper>
            {`เครดิต : ${amount}`}
            <Avatar src={avatar} />
            {email}
          </UserDetailWrapper>
          <MenuHamburger onClick={() => setShowSidebar(true)}>
            เมนู&nbsp;
            <i className="fas fa-bars" />
          </MenuHamburger>
          <OutSideBar>
            <SideBar active={showSidebar}>
              <Iwrapper>
                <i className="fas fa-times fa-2x" onClick={closeSideBar} />
              </Iwrapper>
              <Row className="row col-12">
                <div className="col-12 col-md-6">
                  <PanelBtn
                    onClick={closeSideBar}
                    imgSrc={dashboard}
                    text="แดชบอร์ด"
                    to={privatePath.dashboard}
                  />
                </div>
                <div className="col-12 col-md-6">
                  <PanelBtn
                    onClick={closeSideBar}
                    imgSrc={withdraw}
                    text="ถอนเงิน"
                    to={privatePath.withdraw}
                  />
                </div>
                <div className="col-12 col-md-6">
                  <PanelBtn
                    onClick={closeSideBar}
                    imgSrc={log}
                    text="ประวัติ"
                    to={privatePath.history}
                  />
                </div>
                <div className="col-12 col-md-6">
                  <PanelBtn
                    onClick={closeSideBar}
                    imgSrc={setting}
                    text="ตั้งค่าบัญชี"
                    to={privatePath.setting}
                  />
                </div>
                <div className="col-12 col-md-6">
                  <PanelBtn
                    onClick={() => {
                      clearUserToken();
                      clearUserId();
                      setUserInfo({});
                      closeSideBar();
                      history.push(publicPath.login);
                    }}
                    imgSrc={logout}
                    text="ออกจากระบบ"
                    // to={publicPath.login}
                  />
                </div>
              </Row>
            </SideBar>
            <Overlay active={showSidebar} onClick={closeSideBar} />
          </OutSideBar>
        </DeskTopWrapper>
        <MobileRowWrapper>
          <ButtonGroup />
        </MobileRowWrapper>
      </>
    );
  });
};

export default withApiWrapper(PrivateNavbar);
