import React from "react";
import styled from "styled-components";
import { useObserver } from "mobx-react-lite";
import stateContext from "../../state";
import Wrapper from "../../components/page-wrapper";
import withApiWrapper from "../../utils/withApiWrapper";
import PanelBtn from "../../components/panel-button";
import { privatePath } from "../../utils/route-constants";

import avatar from "../../images/avatar.png";
import deposit from "../../images/deposit.png";
import withdraw from "../../images/withdraw.png";
import log from "../../images/log.png";
import setting from "../../images/setting.png";
import { getUserId } from "../../utils/session";

const Avatar = styled.img`
  width: 50px;
  float: left;
  vertical-align: middle;
  border-style: none;
`;

const UserBar = styled.div`
  align-items: center;
  margin: 0 0 15px;
  padding: 15px;
  border-radius: 20px 0;
  background: linear-gradient(
    120deg,
    rgba(1, 166, 253, 1) 0%,
    rgba(23, 208, 207, 1) 80%
  );
  color: #fff;
`;

const AvatarBar = styled.div`
  padding: 0;
`;

const AvatarText = styled.div`
  display: inline-block;
  margin: 4px 0 4px 15px;
`;

const UserId = styled.span`
  display: block;
  line-height: 1.2;
  font-size: 18px;
  ${props => props.black && "color: #094978;"}
`;

const CreditBar = styled.div`
  border-top: 1px solid rgba(255, 255, 255, 0.6);
  padding: 15px 0 0;
  margin-top: 15px;
  @media (min-width: 768px) {
    border-top: 0;
    border-left: 1px solid rgba(255, 255, 255, 0.6);
    padding: 0 0 0 15px;
    margin: 0;
  }
`;

const P = styled.p`
  margin: 0;
  line-height: 1;
`;

const Credit = styled.h4`
  font-size: 36px;
  line-height: 1;
  margin: 0;
  color: #094978;
  font-family: "Roboto", sans-serif;
  font-weight: 600;
`;

const ContentSector = styled.div`
  margin: 0;
  padding: 0;
`;

const TitleBar = styled.div`
  margin-bottom: 20px;
  width: 100%;
  position: relative;
  :after {
    position: absolute;
    content: "";
    bottom: 12px;
    height: 3px;
    width: 100%;
    right: 0;
    background-color: #17d0cf;
  }
`;

const Title = styled.h3`
  margin: 0;
  font-size: 24px;
  font-weight: 600;
  display: inline-block;
  background-color: #fff;
  z-index: 9;
  position: relative;
  padding-right: 15px;
`;

const Row = styled.div`
  @media (min-width: 767px) {
    [class*="col-"]:nth-child(3n-2) {
      padding: 0 10px 0 0;
    }
    [class*="col-"]:nth-child(3n-1) {
      padding: 0 5px;
    }
    [class*="col-"]:nth-child(3n) {
      padding: 0 0 0 10px;
    }
  }

  @media (max-width: 768px) {
    .detail-col .row [class*="col-"]:nth-child(2n-1) {
      padding: 0 10px 0 0;
    }
    .detail-col .row [class*="col-"]:nth-child(2n) {
      padding: 0 0 0 10px;
    }
  }
`;

const Dashboard = ({ isLoading, isError, apiCall, closeErr }) => {
  const { userInfo, setUserInfo } = React.useContext(stateContext);
  const fetchUserInfo = async () => {
    const res = await apiCall({
      method: "get",
      path: `/wallet-owner/${getUserId()}`
    });
    if (res) {
      setUserInfo(res);
    }
  };
  React.useEffect(() => {
    fetchUserInfo();
  }, []);

  return useObserver(() => {
    const { email, firstName, lastName, amount } = userInfo;
    return (
      <Wrapper isLoading={isLoading} isError={isError} closeErr={closeErr}>
        <UserBar className="row gradient-bg user-detail-col col-12">
          <AvatarBar className="col-12 col-sm-6 col-left">
            {/* <div className="avatar"> */}
            <Avatar src={avatar} alt="" />
            <AvatarText className="avatar-text">
              <UserId className="userid">{email}</UserId>
              <UserId black>{`${firstName} ${lastName}`}</UserId>
            </AvatarText>
          </AvatarBar>
          <CreditBar className="col-12 col-sm-6 col-right">
            <P>เครดิต</P>
            <Credit className="credit-count">{amount}</Credit>
          </CreditBar>
        </UserBar>
        <ContentSector className="row detail-col deposit-detail col-12">
          <TitleBar className="section-title headline">
            <Title>ช่องทางการฝากเงิน</Title>
          </TitleBar>
          <Row className="row col-12">
            <div className="col-6 col-md-3">
              <PanelBtn
                imgSrc={deposit}
                text="ฝากเงินออโต้"
                to={privatePath.deposit}
              />
            </div>
          </Row>
        </ContentSector>
        <ContentSector className="row detail-col deposit-detail col-12">
          <TitleBar className="section-title headline">
            <Title>เมนู</Title>
          </TitleBar>
          <Row className="row col-12">
            <div className="col-6 col-md-3">
              <PanelBtn
                imgSrc={withdraw}
                text="ถอนเงิน"
                to={privatePath.withdraw}
              />
            </div>
            <div className="col-6 col-md-3">
              <PanelBtn imgSrc={log} text="ประวัติ" to={privatePath.history} />
            </div>
            <div className="col-6 col-md-3">
              <PanelBtn
                imgSrc={setting}
                text="ตั้งค่าบัญชี"
                to={privatePath.setting}
              />
            </div>
          </Row>
        </ContentSector>
      </Wrapper>
    );
  });
};

export default withApiWrapper(Dashboard);
