import React from "react";
import { types } from "mobx-state-tree";

const userType = types.model("user", {
  firstName: "",
  lastName: "",
  dateOfBirth: "",
  email: "",
  phoneNumber: "",
  sex: "",
  allowBonus: true,
  amount: "",
  bankAccountNumber: "",
  bankName: ""
});

const mainStore = types
  .model({
    user: types.optional(userType, {})
  })
  .actions(self => ({
    setUserInfo(info) {
      self.user = info;
    }
  }))
  .views(self => ({
    get userInfo() {
      return self.user;
    }
  }));

export const store = mainStore.create();

export const stateContext = React.createContext(store);

export default stateContext;
